<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process224"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span><span class="rvts50"> </span><span class="rvts17">血月岛</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:279,Y:396</span><span class="rvts21">，血月岛</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索血月岛</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1288.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">血月岛位于死神海岸的北部，需要穿越一条海洋。在死神海岸西北处，有一处码头（</span><span class="rvts20">X:163,Y:350</span><span class="rvts21">），一名不死族船夫可以把我们带到血月岛上。</span>
<br><span class="rvts21">然而这个船夫非常的狡诈，索要较高的同行费用（还价成功后降到</span><span class="rvts20">100</span><span class="rvts21">金）。并且当他把你运到血月岛后，他会掳走你身上的钱币和物品。打败他后，可以使用他的小船渡过这片海域。</span>
<br><span class="rvts21">注意，即便可以使用小船自由同行。但所有的非不死族玩家都将瞬间毙命于死亡烟雾，因此这并不是去血月岛的最佳路径。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1289.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在通往血月岛的断桥处（</span><span class="rvts20">X:357,Y:341</span><span class="rvts21">）使用源力视野，可以看到隐藏的路径。角色可以利用跳跃，传送，飞翔等多种位移技能通过该桥，最终也能到达血月岛。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1290.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">血月岛的传送点位于血月岛东部，一旦一名玩家到达此地激活了该传送点，其余角色可以使用</span><span class="rvts20"></span><span class="rvts21">路点</span><span class="rvts20"></span><span class="rvts21">传送的方式，一起来到小岛上。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1291.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在从</span><span class="rvts20"></span><span class="rvts21">隐形的桥</span><span class="rvts20"></span><span class="rvts21">到达血月岛的大门处，可以找到地上的血月岛隐藏地形地图。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1292.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">血月岛中部有</span><span class="rvts20">3</span><span class="rvts21">处被诅咒的雕像，破坏雕像的前提是先要找到一本叫做《华丽诗集》的书，这本书位于血月岛的很多地方。其中，小岛东北部有一处黑环势力占领的区域，有这本书。在血月岛先祖之树的那帮黑环团伙中，也有这本书。</span>
<br><span class="rvts21">另外，在接贾韩的支线</span><span class="rvts20"></span><span class="rvts21">邪恶之物的猎人（</span><span class="rvts20">A Hunter of Wicked Thing</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的屋子内，也有这本书。这本书需要有</span><span class="rvts20"></span><span class="rvts21">学者</span><span class="rvts20"></span><span class="rvts21">标签的角色读懂后，才可以用于破除雕像的诅咒。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1293.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在血月岛东部有一处锻造台，这里可以制作银质杠杆。银质杠杆会用于完成</span><span class="rvts20"></span><span class="rvts21">被遗忘者和被诅咒者（</span><span class="rvts20">The Forgotten and the Damned</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">支线中的三处隐匿地下室。</span>
<br><span class="rvts21">分别对应了</span><span class="rvts20"></span><span class="rvts21">受苦者（</span><span class="rvts20">Bound By Pain</span><span class="rvts21">），宁静者（</span><span class="rvts20">The Silent One</span><span class="rvts21">），勇士的错觉（</span><span class="rvts20">The Delusion of Grandeur</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">三个支线任务。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1294.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">小岛上有几处散落的银锭，可以用来制作银质杠杆。如果找不到足够的银锭，可以尝试使用银质杯子制作。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1295.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">三处需要银质杠杆的洞穴，必须是携带银质杠杆的角色与它互动，才可以成功合成杠杆门阀。使用杠杆前，需要使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">查看守卫洞穴的灵魂，说服他或杀掉他，才会使看守失去对杠杆的源力控制，这样才能打开对应的石门。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



